import popup from '../../components/popup.vue'
export default {
	data() {
		return {
			code: 0,
			token:'',
			user:{},
			show:false,
			title:'您的专属邀请码'
		}
	},
	components:{
		popup
	},
	onLoad() {
		var that=this
		uni.login({
		  success (res) {
		    if (res.code) {
				that.code=res.code
		    } else {
		      console.log('登录失败！' + res.errMsg)
		    }
		  }
		})
		uni.getStorage({
		    key: 'token',
		    success: function (res) {
		        that.token=res.data
		    }
		});
		uni.getStorage({
		    key: 'user',
		    success: function (res) {
		        that.user=res.data
		    }
		});
	},
	methods: {
		getuserinfo(e){
			var that=this
			uni.getSetting({
			  success(res) {
			    if (!res.authSetting['scope.userInfo']) {
			      uni.authorize({
			        scope: 'scope.userInfo',
			        success () {
			        }
			      })
			    }else{
					uni.request({
					    url: getApp().globalData.APIurl+'/api/wxapp/index/login', 
					    data: {
					        code: that.code,
							encrypted_data:e.detail.encryptedData,
							iv:e.detail.iv
					    },
					    header: {
					        'Token': '' ,
							'Device-Type':'wxapp'
					    },
					    success: (res) => {
							that.token=res.data.data.token;
							that.user=res.data.data.user;
							uni.setStorage({
							    key: 'token',
							    data: res.data.data.token,
							    success: function () {
							        console.log('success');
							    }
							});
							uni.setStorage({
							    key: 'user',
							    data: res.data.data.user,
							    success: function () {
							        console.log('success');
							    }
							});
							if(!res.data.data.user.mobile){
								uni.navigateTo({
								    url: '/pages/login/index'
								});
							}
					    }
					});
				}
			  }
			})
		},
		invition(){
			this.show=true
		},
		cancalFun(){
		  this.show=false;
		},
		confirmFun(){
			this.show=false;
		},
		copy(){
			uni.setClipboardData({
			    data: this.user.user_activation_key,
			    success: function () {
			        uni.showToast({
			            title: '复制成功，快去分享给你的朋友们吧',
			        	icon:'none',
			            duration: 2000,
			        	success:function(){
			        		
			        	}
			        });
			    }
			});
		}
	}
}
